import Icon from "./Icon.vue";

export default {

	components: {Icon},

	name: "latte-tab-bar",

	inject: ["tabContainer"],

	data()
	{
		return {
			current: 0,
			tabs: []
		};
	},

	mounted()
	{
		this.tabContainer.$on("change", current => this.onTabChange(current));
		this.tabContainer.updateTabBars();
	},

	methods: {

		click(index)
		{
			this.tabContainer.current = index;
		},

		onTabChange(current)
		{
			this.current = current;
		}

	}

}

